import { Controller, Commands } from '@ali/4ever-cangjie';

export default function updateOnlineVideo(
  editor: Controller,
  onlineVideo,
  data,
) {
  const newData = {
    ...onlineVideo.data,
    ...data,
  };
  return editor.command(Commands.setNodeByKey, onlineVideo.key, {
    data: {
      ...newData,
    },
  });
}
